#game{
  display: flex;
  flex-direction: column; 
  margin:auto; 
  button{
    width:100%;
    height:100%;
  } 
  .cards{
    display: flex;
  } 
  li{
    width:50px;
    height:50px;
    list-style: none;
  }
  ul {
    display:flex; 
    margin:0;
    padding:0;
  }

}


